import React from "react";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import ru from 'date-fns/locale/ru';
import "./search.css"

export class Search extends React.Component {
    constructor(props) {
        super(props);
        this.state={
            city: "",
            keyword: "",
            date: "",
            organisation: ""
        }
    }

    handleChange = date => {
        this.setState({
            date: date
        });
    };

    render() {
        return(
            <div className={"search-container"}>
                <div className={"subheadder"}>
                    Найти стажировку
                </div>
                <div style={{height: "20px"}}/>
                <input placeholder={"Ваша цель"} style={{width: "250px"}} onChange={(event) => this.setState({keyword: event.target.value})}/>
                <div style={{height: "20px"}}/>
                <div style={{display: "flex", justifyContent: "space-between"}}>
                    <div style={{display: "flex", flexDirection: "column", width: "175px"}}>
                        <div className={"H2"} style={{width: "100px"}}>
                            Город
                        </div>
                        <input placeholder={"Пекин"} onChange={(city) => this.setState({city: city.target.value})}/>
                    </div>
                    <div style={{display: "flex", flexDirection: "column", width: "175px"}}>
                        <div className={"H2"} style={{width: "100px"}}>
                            Дата
                        </div>
                        <DatePicker
                            locale={ru}
                            dateFormat="dd.MM.yyyy"
                            selected={this.state.date}
                            onChange={this.handleChange}
                            placeholderText={this.state.date}
                        />
                    </div>
                </div>
                <div className={"H2"}>
                    Организация
                </div>
                <input placeholder={"Университет"} style={{width: "250px"}} onChange={(event) => this.setState({organisation: event.target.value})}/>
                <div style={{height: "20px"}}/>
                <button className={"find"} onClick={() => {this.props.onClick(this.state)}}>
                    Найти
                </button>
            </div>
        )
    }
}