import React from "react";
import "./news-page.css"
import NewsContainer from "./news-container/newsContainer"

export class NewsPage extends React.Component {
    constructor(props) {
        super(props);
        this.state={
            news:[],
            last: ""
        };

        this.getNews();
    }

    getNews = function () {
        let url = "https://134.209.236.129:1337/api/news/";
        fetch (url)
            .then(response => {
                console.log(response)
                return response.json()
            })
            .then(json => {
                console.log(json);
                let hrf = window.location.href.split("/");
                let last = hrf[hrf.length-1];
                let preLast = hrf[hrf.length-2];
                let articles = json;
                if(preLast == "news"){
                    articles = articles.filter((el) =>
                        el.tag == "pol" ||
                        el.tag == "eco" ||
                        el.tag == "cul" ||
                        el.tag == "peo"
                    )
                }
                if(preLast == "journey"){
                    articles = articles.filter((el) =>
                        el.tag == "tri"
                    )
                }
                if(preLast == "culture"){
                    articles = articles.filter((el) =>
                        el.tag == "arc" ||
                        el.tag == "lit" ||
                        el.tag == "art" ||
                        el.tag == "cin"
                    )
                }
                if(preLast == "history"){
                    articles = articles.filter((el) =>
                        el.tag == "inf" ||
                        el.tag == "how" ||
                        el.tag == "svn" ||
                        el.tag == "dis"
                    )
                }
                if(last != "all"){
                    articles = articles.filter((el) => el.tag == last)
                }
                this.setState({news: articles})
            })
            .catch((error) => {
                console.error(error);
            });
    };


    render() {

        let hrf = window.location.href.split("/");
        let last = hrf[hrf.length-1];
        if(last != this.state.last){
            this.setState({last: last})
            this.getNews()
        }

        var background = [];

        var news = [];

        for(let i = 0; i < this.state.news.length; i++){
            background.push(
                <div style={{height: "500px", backgroundColor: ((i%2 === 0) ? "#4D4D4D" : "#EDEBEB")}}/>
            )

            news.push(
                <NewsContainer obj={this.state.news[i]} inverse={(i%2 !== 0)}/>
            )

        }

        hrf = window.location.href.split("/");
        let preLast = hrf[hrf.length-2];

        return(
            <div className={"news-page"}>
                <div className={"header-extension"}>
                    <div className={"H1"}>
                        {preLast == "news" ? "Новости":
                            preLast == "journey" ? "Путешествие по Китаю":
                                preLast == "culture" ? "Культура":
                                    preLast == "history" ? "История": ""
                        }
                    </div>
                </div>
                {background}
                <div className={"news"}>
                    {news}
                </div>
            </div>
        )
    }
}