import React from 'react';
import "./intern-ship.css"


function InternShip(props) {
    let obj = props.obj;
    return (

        <div className={"intern-ship "}>
            <button onClick={() => window.location.href = ("/probation/" + obj.id || "")}/>
            <div className={"image-container"}>
                <img src={obj.image1}/>
            </div>
            <div className={"H2"}>
                {obj.name}
            </div>
            <div className={"container"}>
                <div className={"speciality"}>
                    Специальность
                </div>
                <div className={"description"}>
                    {obj.description}
                </div>
            </div>
            <div className={"date T0"} style={{fontSize: "15px"}}>
                {obj.start + " - " + obj.end}
            </div>
        </div>
    );
}

export default InternShip;
