import React from 'react';
import {Header} from "../../../components/headder/headder";
import {Footer} from "../../../components/footer/footer";

import './Story.css'

export class Story extends React.Component {
    constructor(props) {
        super(props);
        this.state={
            story: {text:""}
        };

        this.getNews();
        this.id_name_dict = {
            [-1]: "Путишествие по китаю",
            [-2]: "Статистика",
            [-3]: "Архитектура",
            [-4]: "Литература",
            [-5]: "Живопись",
            [-6]: "Кино",
            [-7]: "О китайцах",
            [-8]: "Как они думают",
            [-9]: "Юг и Север",
            [-10]: "Непонимание",
        }
    }

    getNews = function () {
        let url = "http://134.209.236.129:1337/api/news/";
        console.log(url);
        fetch (url)
            .then(response => {
                console.log(response);
                return response.json()
            })
            .then(json => {
                console.log(json);
                let hrf = window.location.href.split("/");
                let id = hrf[hrf.length-1];
                let story;
                if(Number(id) < 0){
                    story = json.find((el) => el.title == this.id_name_dict[Number(id)]);
                }else{
                    story = json.find((el) => el.id == id);
                }
                if(!story){
                    story = {
                        text: "not_found"
                    }
                }
                this.setState({story: story})
            })
            .catch((error) => {
                console.error(error);
            });
    };


    render() {
        return(
            <div>
                <Header/>
                <div className={"story"}>
                    <div dangerouslySetInnerHTML={{ __html: this.state.story.text }} />
                </div>
                <Footer/>
            </div>
        )
    }
}

export default Story;
