import React from 'react';

import Slider from "react-slick";

import InternShip from "../../InternShip/InternShip"
import "./carousel.css"

class Carousel extends React.Component {

    /*
    purpose:
      shows slider with images
    props:
      images: array of images to show
     */

    constructor(props) {
        super(props);

        this.settings = {
            dots: false,
            infinite: true,
            speed: 300,
            arrows: false
        };

        this.state={
            internships: [
                {
                    id: -3,
                    name: "",
                    start: "00.00.00",
                    condition: "",
                    image1: "https://user-images.githubusercontent.com/194400/49531010-48dad180-f8b1-11e8-8d89-1e61320e1d82.png"
                },
                {
                    id: -2,
                    name: "",
                    start: "00.00.00",
                    condition: "",
                    image1: "https://user-images.githubusercontent.com/194400/49531010-48dad180-f8b1-11e8-8d89-1e61320e1d82.png"
                },
                {
                    id: -1,
                    name: "",
                    start: "00.00.00",
                    condition: "",
                    image1: "https://user-images.githubusercontent.com/194400/49531010-48dad180-f8b1-11e8-8d89-1e61320e1d82.png"
                }
            ],
        };
        this.getInternships();
    }

    getInternships = function () {
        let url = "https://134.209.236.129:1337/api/internships/";
        fetch (url)
            .then(response => {
                return response.json()
            })
            .then(json => {
                this.setState({internships: json})
            })
            .catch((error) => {
                console.error(error);
            });
    };

    render() {

        let internships = [];

        let internshipsContainers = [];

        for(let i = 0; i<this.state.internships.length; i++){
            // console.log("internship");
            // console.log(this.state.internships[i]);
            internships.push(<InternShip obj={this.state.internships[i]} key={this.state.internships[i].id}/>);
            if(internships.length === 3){
                internshipsContainers.push(
                    <div>
                        <div className={"carousel-flex"}>
                            {internships}
                        </div>
                    </div>
                );
                internships = []
            }
        }
        if(internships.length > 0){
            internshipsContainers.push(
                <div key={this.state.internships.length + 1}>
                    <div className={"carousel-flex"}>
                        {internships}
                    </div>
                </div>
            );
        }

        return (
            <div>
                <Slider {...this.settings}>
                    {internshipsContainers}
                </Slider>
            </div>
        )
    }
}

export default Carousel
