import React from 'react';
import Moment from 'react-moment';
import { Route, Link, BrowserRouter as Router, Switch } from 'react-router-dom'
import "./news-box.css"


function NewsBox(props) {
    let obj = props.obj || {
        title: "",
        publication_date: "00.00.00",
        image: "https://user-images.githubusercontent.com/194400/49531010-48dad180-f8b1-11e8-8d89-1e61320e1d82.png",
        short_text: ""
    };
    return (
        <div className={"news-box " + (props.size)} style={{backgroundColor: props.color, color: ((props.color === "#000000")? "white": "black")}}>
            <button onClick={() => window.location.href = ("/story/" + obj.id || "")}/>
            <div className={"T0 publication-date"}>
                <Moment format="DD/MM/YYYY">
                    {obj.publication_date}
                </Moment>
            </div>
            <img src={obj.image} className={"image"}/>
            <div className={"H15"} style={{color: ((props.color === "#000000")? "white": "black")}}>
                {obj.title}
            </div>
            <div className={"text"}>
                {obj.short_text}
            </div>
            <div className={"fade"} style={{ backgroundColor: props.color,
                backgroundImage: "linear-gradient(bottom," +
                " rgba(0, 0, 0, 0)," + props.color + ")"}}/>
        </div>
    );
}

export default NewsBox;
