import React from 'react';
import {Header} from "../../../components/headder/headder";
import {Footer} from "../../../components/footer/footer";

import './heading.css'

export class Heading extends React.Component {
    constructor(props) {
        super(props);
        this.state={
            story: {text:""}
        };

        this.getHeadings()
    }

    getHeadings = function () {
        let url = "https://134.209.236.129:1337/api/headings/";
        fetch (url)
            .then(response => {
                return response.json()
            })
            .then(json => {
                let hrf = window.location.href.split("/");
                let id = hrf[hrf.length-1];
                let story = json.find((el) => el.id == id);
                this.setState({story: story})
            })
            .catch((error) => {
                console.error(error);
            });
    };


    render() {

        return(
            <div>
                <Header/>
                <div className={"story"}>
                    <div dangerouslySetInnerHTML={{ __html: this.state.story.text }} />
                </div>
                <Footer/>
            </div>
        )
    }
}

export default Heading;
