import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

import { Route, Link, BrowserRouter as Router } from 'react-router-dom'

import App from './App';
import News from './News'
import Internships from './Internships'
import Story from './components/pages/story/Story'
import Heading from './components/pages/heading/heading'
import {InternshipPage} from "./components/pages/internships/internship-page";
import * as serviceWorker from './serviceWorker';

const routing = (
    <Router>
        <div>
            <Route exact path="/" component={App} />
            <Route path="/news" component={News} />
            <Route path="/journey" component={News} />
            <Route path="/culture" component={News} />
            <Route path="/history" component={News} />
            <Route path="/internships" component={Internships} />
            <Route path="/story" component={Story} />
            <Route path="/heading" component={Heading} />
            <Route path="/probation" component={InternshipPage} />
        </div>
    </Router>
)
ReactDOM.render(routing, document.getElementById('root'))


// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
