import React from 'react';
import './App.css';
import {Header} from "./components/headder/headder";
import {Footer} from "./components/footer/footer";
import {MainPage} from "./components/pages/main/main-page";
import "./common/common.css"


class App extends React.Component {
    constructor(props) {
        super(props);
        this.state= {
            state: null
        }
    }

    onClick = function (state) {
        this.setState({state: state});
        // console.log(state)
    };

    render() {
        return (
            <div className="App">
                <Header onClick={() => this.onClick(null)}/>
                <MainPage state={this.state.state} onClick={(state) => this.onClick(state)}/>
                <Footer/>
            </div>
        );
    }
}

export default App;
