import React from "react";
import "./main-page.css"
import {Search} from "../../headder/search/search";
import {Book} from "./book";
import NewsBox from "./NewsBox/NewsBox"
import Heading from "./Heading/Heading"
import {Header} from "../../headder/headder";
import Carousel from "./carousel/carousel";
import InternShip from "../InternShip/InternShip";
import {InternshipsPage} from "../internships/Internships-page";

export class MainPage extends React.Component {
    constructor(props) {
        super(props);
        this.state= {
            news: [
                {
                    title: "",
                    publication_date: "00.00.00",
                    image: "https://user-images.githubusercontent.com/194400/49531010-48dad180-f8b1-11e8-8d89-1e61320e1d82.png",
                    short_text: ""
                },
                {
                    title: "",
                    publication_date: "00.00.00",
                    image: "https://user-images.githubusercontent.com/194400/49531010-48dad180-f8b1-11e8-8d89-1e61320e1d82.png",
                    short_text: ""
                },
                {
                    title: "",
                    publication_date: "00.00.00",
                    image: "https://user-images.githubusercontent.com/194400/49531010-48dad180-f8b1-11e8-8d89-1e61320e1d82.png",
                    short_text: ""
                },
                {
                    title: "",
                    publication_date: "00.00.00",
                    image: "https://user-images.githubusercontent.com/194400/49531010-48dad180-f8b1-11e8-8d89-1e61320e1d82.png",
                    short_text: ""
                },
                {
                    title: "",
                    publication_date: "00.00.00",
                    image: "https://user-images.githubusercontent.com/194400/49531010-48dad180-f8b1-11e8-8d89-1e61320e1d82.png",
                    short_text: ""
                },
            ],
            headings: [
                {
                    title: "",
                    author: {
                        image: "https://user-images.githubusercontent.com/194400/49531010-48dad180-f8b1-11e8-8d89-1e61320e1d82.png",
                        name: ""
                    },
                    short_text: "",
                },
                {
                    title: "",
                    author: {
                        image: "https://user-images.githubusercontent.com/194400/49531010-48dad180-f8b1-11e8-8d89-1e61320e1d82.png",
                        name: ""
                    },
                    short_text: "",
                }
            ],
        }
        this.getNews();
        this.getHeadings();

    }

    getNews = function () {
        let url = "http://134.209.236.129:1337/api/news/";
        fetch (url)
            .then(response => {
                return response.json()
            })
            .then(json => {
                this.setState({news: json})
            })
            .catch((error) => {
                console.error(error);
            });
    };

    getHeadings = function () {
        let url = "https://134.209.236.129:1337/api/headings/";
        fetch (url)
            .then(response => {
                return response.json()
            })
            .then(json => {
                this.setState({headings: json})
            })
            .catch((error) => {
                console.error(error);
            });
    };

    render() {

        let headings=[];

        for(let i=0; i<this.state.headings.length; i++){
            headings.push(<Heading obj={this.state.headings[i]}/>)
        }

        return(
            <div>
            {(this.props.state != null) ?
                <InternshipsPage state={this.props.state}/>
                :
                <div className={"main-page"}>
                    <div className={"header-extension"}>
                        <div style={{
                            display: "flex",
                            justifyContent: "space-around"
                        }}>
                            <div className={"text-flex"}>
                                <div className={"title"}>
                                    КИТАЙ СЕГОДНЯ
                                </div>
                                <div className={"divider"}/>
                                <div className={"aux-text"}>
                                    Молодежный портал о культуре, людях,
                                    впечатлниях, новостях, учебе и работе в
                                    Китае
                                </div>
                            </div>
                            <Search
                                onClick={(state) => this.props.onClick(state)}/>
                        </div>
                    </div>


                    <div className={"news"}>
                        <Book/>
                        <NewsBox obj={this.state.news.filter((el) =>
                            el.tag == "arc" ||
                            el.tag == "lit" ||
                            el.tag == "art" ||
                            el.tag == "cin"
                        )[0]} size={"big"}
                                 color={"#C4C4C4"}/>
                    </div>
                    <div className={"news"}>
                        <NewsBox obj={this.state.news.filter((el) =>
                            el.tag == "inf" ||
                            el.tag == "how" ||
                            el.tag == "svn" ||
                            el.tag == "dis"
                        )[0]} size={"medium"}
                                 color={"#000000"}/>
                        <NewsBox obj={this.state.news.filter((el) =>
                            el.tag == "pol" ||
                            el.tag == "eco" ||
                            el.tag == "cul" ||
                            el.tag == "peo"
                        )[0]} size={"medium"}
                                 color={"#B73F3F"}/>
                    </div>
                    <div className={"news"}>
                        <NewsBox obj={this.state.news.filter((el) =>
                            el.tag == "pol" ||
                            el.tag == "eco" ||
                            el.tag == "cul" ||
                            el.tag == "peo"
                        )[1]} size={"small"}
                                 color={"#C4C4C4"}/>
                        <NewsBox obj={this.state.news.filter((el) =>
                            el.tag == "pol" ||
                            el.tag == "eco" ||
                            el.tag == "cul" ||
                            el.tag == "peo"
                        )[2]} size={"big"}
                                 color={"#000000"}/>
                    </div>

                    <div className={"H1"}>
                        Новые стажировки
                    </div>

                    <Carousel/>

                    <div className={"H1"}>
                        Полезные рубрики
                    </div>

                    <div className={"headings"}>
                        {headings}
                    </div>
                </div>
            }
            </div>
        )
    }
}