import React from 'react';
import Moment from 'react-moment';
import "./news-container.css"



function NewsContainer(props) {
    let obj = props.obj;

    let tags = {
        "pol": "Политика",
        "eco": "Экономика",
        "cul": "Культура",
        "peo": "Люди",
        "arc": "Архитектура",
        "lit": "Литература",
        "art": "Живопись",
        "cin": "Кино",
        "inf": "О китайцах",
        "how": "Как они думают?",
        "svn": "Юг и Сервер",
        "dis": "Непонимание",
        "tri": "Путешествие по Китаю",
}

    return (
        <div className={"news-container " + (props.inverse ? "inverse": "")}>
            <button onClick={() => window.location.href = ("/story/" + obj.id || "")}/>
            {props.inverse ? <img src={obj.image}/> : null}
            <div className={"text-container"}>
                <div className={"date-container"}>
                    <div className={"tag"}>
                        {tags[obj.tag]}
                    </div>
                    <div className={"date"}>
                        <Moment format="YYYY/MM/DD">
                            {obj.publication_date}
                        </Moment>
                    </div>
                </div>
                <div>
                    <div className={"H15"}>
                        {obj.title}
                    </div>
                    <div className={"text"}>
                        {obj.short_text}
                    </div>
                </div>
            </div>
            {!props.inverse ? <img src={obj.image}/> : null}
        </div>
    );
}

export default NewsContainer;
