import React from "react";
import "./internships-page.css";
import InternShip from "../InternShip/InternShip"

export class InternshipsPage extends React.Component {
    constructor(props) {
        super(props);
        this.state={
            internships:[],

            city: (this.props.state && this.props.state.city) ? this.props.state.city : "Город",
            length: "Длительность",
            speciality: "Специальность",
            date: this.props.state ? this.props.state.date || "" : "",
            organisation: this.props.state ? this.props.state.organisation || "" : "",
            tag: this.props.state ? this.props.state.keyword || "" : "",

            first: true,
            second: true,
            third: true,
        };

        if(this.props.state){
            for(let i=0;i < Object.keys(this.props.state).length; i++){
                let name = Object.keys(this.props.state)[i]
                this.setState({[name]: this.props.state[name]})
            }
        }
        this.getInternships();

    }

    getInternships = function () {
        let url = "https://134.209.236.129:1337/api/internships/";
        fetch (url)
            .then(response => {
                return response.json()
            })
            .then(json => {
                if(this.state.city != "Город"){
                    json = json.filter((el) => el.city == this.state.city)
                }
                if(this.state.speciality != "Специальность"){
                    json = json.filter((el) => el.description == this.state.speciality)
                }
                if(this.state.length != "Длительность"){
                    json = json.filter((el) =>  (Math.abs(new Date(el.start) - new Date(el.end)) / 86400000 ) <= this.state.length)
                }
                if(this.state.organisation != ""){
                    console.log(json)
                    json = json.filter((el) => el.organization == this.state.organisation)
                }
                if(this.state.date != ""){
                    json = json.filter((el) =>  new Date(el.start) < new Date(this.state.date) && new Date(this.state.date) < new Date(el.end))
                }
                if(this.state.tag != ""){
                    json = json.filter((el) =>  {
                        let alltext = ""
                        let fields = Object.keys(el)
                        for(let i=0; i<fields.length;i++){
                            let text = el[fields[i]]
                            alltext += " " + text
                        }
                        return alltext.includes(this.state.tag)
                    })
                }
                this.setState({internships: json})
            })
            .catch((error) => {
                console.error(error);
            });
    };

    onCityClick = function(city){
        this.setState({city: city, second: true})
        this.getInternships()
    }
    onSpecialityClick = function(speciality){
        this.setState({speciality: speciality, third: true})
        this.getInternships()
    }
    onLengthClick = function(time){
        if(time != ""){
            this.setState({length: time, first: true})
        }else{
            this.setState({length: "Длительность", first: true})
        }
        this.getInternships()
    }

    render() {

        var internships1 = [];
        for(let i=0;i<this.state.internships.length; i++){
            internships1.push(
                <InternShip obj={this.state.internships[i]}/>
            )
        }

        return(
            <div className={"internships-page"}>
                <div className={"header-extension"}>
                    <div className={"H1"}>
                        Стажировки
                    </div>
                    <div className={"buttons"}>
                        <div>
                            <button onClick={() => this.setState({first: !this.state.first})}>
                                <div className={"title"}>
                                    {this.state.length + ((this.state.length != "Длительность") ? " дней": "")}
                                </div>
                                <div className={"arrow"}>
                                    ▼
                                </div>
                            </button>
                            <div className={"bubble" + (this.state.first ? " hidden": "")}>
                                <input
                                    placeholder={"дней"}
                                    onBlur={(event) => this.onLengthClick(event.target.value)}
                                />
                            </div>
                        </div>
                        <div>
                            <button onClick={() => this.setState({second: !this.state.second})}>
                                <div className={"title"}>
                                    {this.state.city}
                                </div>
                                <div className={"arrow"}>
                                    ▼
                                </div>
                            </button>
                            <div className={"bubble" + (this.state.second ? " hidden": "")}>
                                <button onClick={() => this.onCityClick("Город")}>
                                    Любой
                                </button>
                                <button onClick={() => this.onCityClick("Пекин")}>
                                    Пекин
                                </button>
                                <button onClick={() => this.onCityClick("Шанхай")}>
                                    Шанхай
                                </button>
                                <button onClick={() => this.onCityClick("Гуанджоу")}>
                                    Гуанджоу
                                </button>
                                <button onClick={() => this.onCityClick("Сиань")}>
                                    Сиань
                                </button>
                                <button onClick={() => this.onCityClick("Цунцин")}>
                                    Цунцин
                                </button>
                                <button onClick={() => this.onCityClick("Тяньцзинь")}>
                                    Тяньцзинь
                                </button>
                            </div>
                        </div>
                        <div>
                            <button onClick={() => this.setState({third: !this.state.third})}>
                                <div className={"title"}>
                                    {this.state.speciality}
                                </div>
                                <div className={"arrow"}>
                                    ▼
                                </div>
                            </button>
                            <div className={"bubble" + (this.state.third ? " hidden": "")}>
                                <button onClick={() => this.onSpecialityClick("Специальность")}>
                                    любое
                                </button>
                                <button onClick={() => this.onSpecialityClick("международные отношения")}>
                                    Международные отношения
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
                <div className={"internships"}>
                    {internships1}
                </div>
            </div>
        )
    }
}