import React from "react";
import "./book.css"

export class Book extends React.Component {
    constructor(props) {
        super(props);
        this.state={
            open: false,
            quote: {}
        };
        this.getQuote()
    }

    getQuote = function () {
        let url = "https://134.209.236.129:1337/api/quotes/random_quote/";
        fetch (url)
            .then(response => {
                return response.json()
            })
            .then(json => {
                this.setState({quote: json})
            })
            .catch((error) => {
                console.error(error);
            });
    };

    render() {
        return(
            <div className={"book-container"}>
                <div className={"page cover"}/>
                <div className={"page middle"}/>
                <div className={"tongue"}/>
                <div className={"page quote-container"}>
                    <div className={"quotes"} style={{top: "-12px", left: "32px"}}>
                        “
                    </div>
                    <div className={"quote"}>
                        {this.state.quote.text}
                    </div>
                    <div className={"quotes"} style={{transform: "rotate(-180deg)", top: "423px", left: "256px"}}>
                        “
                    </div>
                </div>
                <div className={"page cover front" + (this.state.open ? " open": "")}>
                    <div className={"quote-text"}>
                        Цитата дня
                    </div>
                    <div className={"quote-name"}>
                        {this.state.quote.author}
                    </div>
                </div>
                <button onClick={() => this.setState({open: !this.state.open})}/>
            </div>
        )
    }
}