import React from "react";
import "./internship-page.css";
import {Header} from "../../headder/headder";

export class InternshipPage extends React.Component {
    constructor(props) {
        super(props);
        this.state={
            internship: {}
        };
        this.getInternship();
    }

    getInternship = function () {
        let url = "https://134.209.236.129:1337/api/internships/";
        console.log(url);
        fetch (url)
            .then(response => {
                console.log(response);
                return response.json()
            })
            .then(json => {
                console.log(json);
                let hrf = window.location.href.split("/");
                let id = hrf[hrf.length-1];
                let story = json.find((el) => el.id == id);
                this.setState({internship: story})
            })
            .catch((error) => {
                console.error(error);
            });
    };

    render() {

        let obj = this.state.internship

        return(
            <div className={"intern-page"}>
                <Header/>
                <div className={"header-extension"}>
                    <div className={"H1"}>
                        {obj.name}
                    </div>
                </div>

                <div className={"img-flex"}>
                    <img src={obj.image1}/>
                    <img src={obj.image2}/>
                    <img src={obj.image3}/>
                    <img src={obj.image4}/>
                </div>
                <div className={"img-flex"}>
                    <div className={"text-col"}>
                        <div className={"title"}>
                            Когда?
                        </div>
                        <div className={"text"}>
                            {obj.time}
                        </div>
                    </div>
                    <div className={"text-col"}>
                        <div className={"title"}>
                            Где?
                        </div>
                        <div className={"text"}>
                            {obj.place}
                        </div>
                    </div>
                    <div className={"text-col"}>
                        <div className={"title"}>
                            Условия
                        </div>
                        <div className={"text"}>
                            {obj.conditions}
                        </div>
                    </div>
                    <div className={"text-col"}>
                        <div className={"title"}>
                            Рейтинг
                        </div>
                        <div className={"text"}>
                            {obj.rating + " ★"}
                        </div>
                    </div>
                </div>

            </div>
        )
    }
}